*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-family: system-ui,Courier, monospace;
}
.header{
    font-family: Courier, monospace;
    width: 100%;
    height: 30px;
    background-color: aquamarine;
    font-size: 15px;
    padding: 0.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
}
.searchform{
    width:300px;
    padding: 7px 0px 0px 5px;
}
.searchform input[type="text"]{
    font-family: 'Open Sans',sans-serif;
    width: 100%;
    min-height: 35px;
    font-size: 1rem;
    padding: 0.25rem;
    border-radius: 0.25rem;
    outline: none;
}
.nav{
    background-color: #333;
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.nav ul{
    color: white;
    list-style-type: none;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
}
.nav li{
    padding: 1rem;
}

.nav a{
    color: white;
    text-decoration: none;
}
.nav li:hover,
.nav li:focus,
.nav li:hover a,
.nav li:focus a{
    background-color: white;
    color: #333;
}
.postarticle{
    padding: 5px 10px 0px 10px;
}
.postarticle h2
{
    font-size: 20px;
}
.postarticle a{
    text-decoration: none;
    color:black;
}
.postarticle hr{
    margin-top: 10px;
    color: #333;
}
.datetime
{
    font-size: 13px;
}

.findpost{
    padding: 20px;
}
.findpost h2{
font-size: 25px;
}
.postbody{
    margin-top: 10px;
    font-size: 18px;
}
.findpost a{
    list-style-type: none;
}
.newpostform{
    display: flex;
    flex-direction: column;
    padding: 0px 10px 10px 25%;
}
.newpostmain h3{
    margin-top: 5px;
    text-align: center;
}
.newpostform label{
    font-size: 20px;
    font-weight: 400;
    margin-bottom: 5px;
    margin-top: 0px;
}
.newpostform input{
    width: 60%;
    height: 40px;
    padding: 2px 5px 2px 10px;
    border-radius: 3px;
    outline: none;
}
.newpostform textarea{
    width: 60%;
    height: 250px;
    padding: 10px;
    font-size: 15px;
}
.newpostform button{
    width: 100px;
    font-size: 20px;
    margin-top: 10px;
    border-radius: 5px;
    cursor: pointer;
}
.newpostform button:hover,
.newpostform button:focus{
    box-sizing: 5px 5px 5px;
    color: #fff;
    background-color: #333;
}
.footer{
    width: 100%;
    background-color: #333;
    height: 30px;
    color: #fff;
    position: fixed;
    bottom: 0px;
    text-align: center;
    align-items: center;
    padding: 5px;
}
.deletebutton,
.editbutton{
    width: 80px;
    height: 30px;
    padding: 4px;
    margin: 10px;
    list-style-type: none;
    border-radius: 5px;
    outline: none;
    box-shadow: 2px 2px 2px;
    cursor: pointer;
    background-color:rgb(134, 250, 244);
}

.deletebutton:hover,
.deletebutton:focus{
    width:90px;
    height: 40px;
    background-color: rgb(252, 24, 24);
    color: #fff;
}
.editbutton a{
    list-style-type:none ;
}
.editbutton:hover,
.editbutton:focus{
    width:90px;
    height: 40px;
    background-color: #333;
    color: #fff;
}
.about{
    margin: 20px;
    font-size: medium;
    font-weight: 600;
}
.nopost{
    color: red;
}

@media all and (max-width:800px){
    .newpostform input,.newpostform textarea{
        width: 80%;
    }
    .newpostform{
        padding-left: 30px;
        margin-top: 20px;
    }
}